<template>
<div>
  <div class="search">
    <input v-model="keyword" class="search-input" type="text" placeholder="输入城市名或拼音" />
  </div>
  <div
    class="search-content"
    ref="search"
    v-show="keyword"
  >
    <ul>
      <li
        class="search-item border-bottom"
        v-for="item of list"
        :key="item.id"
        @click="handleCityClick(item.name)"
      >
        {{item.name}}
      </li>
      <li class="search-item border-bottom" v-show="hasNoData">
        没有找到匹配数据
      </li>
    </ul>
  </div>
</div>
</template>

<script>
import Bscroll from 'better-scroll'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '', // 输入的关键词，双向绑定
      list: [], // 包含关键词的cities[i]数组
      timer: null // 数据截流
    }
  },
  // 计算属性
  computed: {
    hasNoData () {
      return !this.list.lengths
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      // 没有关键词，列表为空
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = [] // 构建数组
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            // 判断cities里面,是否包含输入keyword,包含的cities[i]构建成数组result
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  methods: {
    handleCityClick (city) {
      // this.$store.dispatch('changeCity', city)
      this.$store.commit('changeCity', city) // 数据少可以直接调用commit对mutations传数据
      this.$router.push('/') // 回到首页
    }
  },
  // 生命周期函数mounted，在页面DOM挂载完之后执行
  mounted () {
    this.scroll = new Bscroll(this.$refs.search)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .search
    height:.72rem
    padding:0 .1rem
    background:$bgColor
    .search-input
      box-sizing:border-box
      width:100%
      height:.62rem
      line-height:.62rem
      padding:0 .1rem
      text-align:center
      border-radius:.06rem
      color:#666
  .search-content
    z-index:1
    overflow:hidden
    position:absolute
    top:1.58rem
    left:0
    right:0
    bottom:0
    background:#eee
    .search-item
      line-height:.62rem
      padding-left:.2rem
      color:#666
      background:#fff
</style>



