<template>
  <div class="container" @click="handleGallaryClick">
    <div class="wrapper">
      <swiper :options="swiperOptions">
        <!-- slides -->
        <swiper-slide
          v-for="(item, index) in imgs"
          :key="index"
        >
          <img class="gallary-img" :src="item" alt="">
        </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    // 外部接收属性，类型Array,default默认是个函数返回一个数组
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOptions: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction', // 分页1/2
        observer: true, // 例如window.resize，Swiper更新。
        observeParents: true // 例如window.resize，Swiper更新。
      }
    }
  },
  methods: {
    handleGallaryClick () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .container >>> .swiper-container
    overflow:inherit
  .container
    display:flex
    flex-direction:column
    justify-content:center
    z-index:99
    position:fixed
    top:0
    left:0
    right:0
    bottom:0
    background:#000
    .wrapper
      height:0
      width:100%
      padding-bottom:100%
      .gallary-img
        width:100%
      .swiper-pagination
        color:#fff
        bottom:-1rem
</style>



