<template>
  <div>
    <city-header></city-header>
    <city-search :cities="cities"></city-search>
    <city-List
      :cities="cities"
      :hot="hotCities"
      :letter="letter"
    >
    </city-List>
      <city-Alphabet
        :cities="cities"
        @change="handleLetterChange"
      >
    </city-Alphabet>
  </div>
</template>

<script>
import axios from 'axios'
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'
export default {
  name: 'City',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data () {
    return {
      cities: {}, // 对象
      hotCities: [], // 数组
      letter: ''
    }
  },
  methods: {
    getCityInfor () {
      axios.get('./api/city.json')
        .then(this.handleGetCityInforSucc)
    },
    handleGetCityInforSucc (res) {
      res = res.data
      // console.log(res)
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
        // console.log(this.cities)
      }
    },
    // 接收子组件Alphabet.vue传来的值
    handleLetterChange (letter) {
      this.letter = letter
      // console.log(letter)
    }
  },
  // 生命周期函数
  mounted () {
    this.getCityInfor()
  }
}
</script>

<style lang="stylus" scoped>

</style>



