<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe600;</span>
      输入城市/游玩/景点
    </div>
    <router-link to='/city'>
      <div class="header-right">
        {{this.$store.state.city}}
        <span class="iconfont arrow-icon">&#xe614;</span>
      </div>
    </router-link>
  </div>
</template>
<script>
export default({
  name: 'HomeHeader'
})

</script>

<style lang="stylus" scoped>
  //样式里面引入样式，引入全局styl文件,@找到src目录，css里面前面需要加~波浪线
  // @import '../../../assets/styles/varibles.styl'
  // @import '~@/assets/styles/varibles.styl'
  @import '~styles/varibles.styl'
  .header
    display:flex
    line-height: $headerHeight
    background:$bgColor
    color:#fff
    .header-left
      width: .64rem
      float: left
      .back-icon
        text-align:center
        font-size:.4rem
    .header-input
        flex:1
        height:.64rem
        line-height:.64rem
        margin-top:.12rem
        margin-left:.2rem
        padding-left:.2rem
        background:#fff
        border-radius:.1rem
        color:#ccc
    .header-right
      width:1.24rem
      float:right
      color:#fff
      text-align:center
      .arrow-icon
        margin-left:-.04rem
        font-size:.24rem
</style>



