import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  // 公用的数据,main.js实例化store,
  // 每个子组件都可以用this.$store.state.city获取组件，公共数据
  state: {
    city: '北京'
  },
  // 子组件通过this.$store.dispatch('changeCity', city)传递来的数据,传递给actions
  // actions通过ctx.commit('changeCity', city),传递给mutations,
  // mutations通过state.city = city,改变state公共数据
  actions: {
    changeCity (ctx, city) {
      // console.log(ctx)
      ctx.commit('changeCity', city)
    }
  },
  mutations: {
    changeCity (state, city) {
      state.city = city
    }
  }
})



