<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="showSwiper">
      <!-- slides -->
      <swiper-slide v-for="item of list" :key="item.id">
        <img class="swiper-img" :src="item.imgUrl" alt="">
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default{
  name: 'HomeSwiper',
  props: {
    list: Array
  },
  data () {
    return {
      // 轮播页码
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true, // 循环轮播
        autoplay: 3000, // 自动切换时长
        speed: 1000, // 滑动速度
        paginationClickable: true // 点击分页轮播
      }
    }
  },
  computed: {
    showSwiper () {
      return this.list.length
    }
  }
}
</script>

<style lang="stylus" scoped>
//>>>穿透效果，后面的class都会添加样式
.wrapper>>> .swiper-pagination-bullet-active
  background:#fff
.wrapper
  overflow:hidden
  width:100%
  height:0
  // 高/宽*100%=31.25%
  padding-bottom:31.2%
  background:#eee
  .swiper-img
    width:100%
</style>



