<template>
  <div id="app">
    <!-- <keep-alive>作页面缓存,
    exclude='Detail：'Detail.vue页面的name='Detail'
    表示这个页面不会作缓存，每次加载都重新请求 -->
    <keep-alive exclude='Detail'>
      <router-view/>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>

</style>



