<template>
  <div  @scroll=handleScroll>
    <router-link
      tag="div"
      to="/"
      class="header-abs"
      v-show="showAbs"
    >
      <div class="iconfont header-abs-back">&#xe624;</div>
    </router-link>
    <div
      class="header-fixed"
      v-show="!showAbs"
      :style="opacityStyle"
    >
      景点详情
      <router-link to='/'>
        <div class="iconfont header-fixed-back">&#xe624;</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      // 滚动渐隐渐显
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      console.log(scroll)
      const top = document.documentElement.scrollTop
      if (top > 60) {
        // 滚动渐隐渐显
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = {opacity}
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  /* 1.配合App.vue的<keep-alive><router-view/></keep-alive>作页面缓存时,可以实现进入页面触发window滚动监听，离开移除监听，activated和deactivated能组合使用；
   2.当使用exclude='Detail'不做页面缓存时,activated和deactivated不能组合使用 */
  // activated () {
  mounted () { // 周期钩子函数，进入页面触发
    // 页面滚动时触发函数handleScroll
    window.addEventListener('scroll', this.handleScroll,true)
  },
  // deactivated () {
  destroyed () { // 销毁事件，离开页面触发
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-abs
    position:absolute
    top:.2rem
    left:.2rem
    width:.8rem
    height:.8rem
    line-height:.8rem
    text-align:center
    border-radius:.4rem
    background:rgba(0,0,0,.8)
    .header-abs-back
      color:#fff
      font-size:.4rem
  .header-fixed
    z-index:2
    position:fixed
    top:0
    left:0
    right:0
    height:$headerHeight
    line-height:$headerHeight
    text-align:center
    font-size:.32rem
    color:#fff
    background:$bgColor
    .header-fixed-back
      position:absolute
      top:0
      left:0
      width:.64rem
      text-align:center
      font-size:.4rem
      color:#fff
</style>



