<template>
  <ul class="list">
   <!--  <li
      class="item"
      v-for="(item,key) of cities"
      :key="key" -->
     <li
      class="item"
      v-for="item of letters"
      :key="item"
      :ref="item"
      @touchstart="handleTouchStart"
      @touchmove="handleTouchMove"
      @touchend="handleTouchEnd"
      @click="handleLetterClick"
    >
      {{item}}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'CityAlphabet',
  props: {
    cities: Object
  },
  computed: {
    // 计算属性，cities构建为letters数组
    letters () {
      const letters = []
      for (let i in this.cities) {
        letters.push(i)
      }
      return letters // ['A','B','C']
    }
  },
  data () {
    return {
      touchStatus: false,
      startY: 0,
      timer: null // 数据截流-move移动频率比较高,16ms内触发清除
    }
  },
  // 页面重新渲染后updated生命周期钩子执行
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  methods: {
    handleLetterClick (e) {
      // console.log(e.target.innerHTML) // 获取点击值
      this.$emit('change', e.target.innerHTML)
    },
    handleTouchStart () {
      this.touchStatus = true
    },
    handleTouchMove (e) {
      if (this.touchStatus) {
        if (this.timer) {
          clearTimeout(this.timer) // 如果存在，就清除timer
        }
        this.timer = setTimeout(() => {
          // const startY = this.$refs['A'][0].offsetTop
          const touchY = e.touches[0].clientY - 79 // -头部高度
          const index = Math.floor((touchY - this.startY) / 20) // 向下取整/每个字母高度
          // console.log(index)
          if (index >= 0 && index < this.letters.length) {
            this.$emit('change', this.letters[index])
          }
        }, 16)
      }
    },
    handleTouchEnd () {
      this.touchStatus = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
.list
  //垂直居中
  display:flex
  flex-direction:column
  justify-content:center
  position:absolute
  top:1.58rem
  right:0
  bottom:0
  width:.4rem
  .item
    line-height:.4rem
    text-align:center
    color:$bgColor
</style>



